import * as React from 'react';
import { Box, Grid2 as Grid } from '@mui/material';
import "./square-image.css"

function SquareImage() {

    return (
        <Grid className='home-row' container spacing={4}>
            <Grid size={1}></Grid>
            
            
            <Grid className='image-tile' size={7}>
                <img className='medium-image' src='https://4kwallpapers.com/images/wallpapers/modern-architecture-look-up-reflection-glass-building-2048x2048-2881.jpg' />
            </Grid>
            <Grid  size={2}>
                <Box className='square-descriptor-text'>
                    <Box className='text-title'>
                        <h1 className='clickable-title'>
                            Project 1
                        </h1> 
                    <p className='clickable-title'>Spring 2024</p>
                </Box>
                <Box className='square-text-desc'>
                    <p>"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."</p>
                </Box>
                </Box>
                
                
            </Grid>
            <Grid size={2}></Grid>
        </Grid>

    );


}

export default SquareImage;