import * as React from 'react';
import { Box, Grid2 as Grid } from '@mui/material';
import "./ultra-wide-image.css"

function UltraWideImage() {

    return (
        <Grid className='home-row' container spacing={2}>
            <Grid size={1}></Grid>
            <Grid className='image-tile' size={10}>
                <img className='image' src='https://cdn.prod.website-files.com/631e594043d13c32e2c927d9/6603aa92a0bdeb4674160730_minimalist-arch-02.jpg' />
            </Grid>
            <Grid size={1}></Grid>
            <Grid size={3}></Grid>
            <Grid className='descriptor-text' size={6}>
                <Box className='text-title'>
                    <h1 className='clickable-title'>Project 1</h1> 
                    <p>Spring 2024</p>
                </Box>
                <Box className='text-desc'>
                    <p>"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."</p>
                </Box>
                
            </Grid>
            <Grid size={3}></Grid>
        </Grid>

    );


}

export default UltraWideImage;