import * as React from 'react';
import { Box, Grid2 as Grid } from '@mui/material';
import "./mobile-tile.css"

function MobileTile() {

    return (
        <Grid className='home-row' container spacing={0}>
            <Grid size={1}></Grid>
            <Grid className='image-tile' size={10}>
                <img className='image' src='https://papers.co/wallpaper/papers.co-vr27-building-architecture-wood-view-pattern-25-wallpaper.jpg' />
            </Grid>
            <Grid size={1}></Grid>
            <Grid size={1}></Grid>
            <Grid className='descriptor-text' size={11}>
                <Box className='text-title'>
                    <h1 className='clickable-title'>Project 1</h1>
                    <em>Spring 2024</em>
                </Box>


            </Grid>
            <Grid size={1}></Grid>
            <Grid size={10}>

                <p className='mobile-text-desc'>"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."</p>

            </Grid>
            <Grid size={1}></Grid>
        </Grid>

    );


}

export default MobileTile;