import * as React from 'react';
import { Box, Grid2 as Grid } from '@mui/material';
import "./medium-wide-image.css"

function MediumWideImage() {

    return (
        <Grid className='home-row' container spacing={2}>
            <Grid size={1.5}></Grid>
            
            <Grid  size={2}>
                <Box className='medium-descriptor-text'>
                    <Box className='text-title'>
                        <h1 className='clickable-title'>
                            Project 1
                        </h1> 
                    <p className='clickable-title'>Spring 2024</p>
                </Box>
                <Box className='medium-text-desc'>
                    <p>"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."</p>
                </Box>
                </Box>
                
                
            </Grid>
            <Grid className='image-tile' size={7}>
                <img className='medium-image' src='https://papers.co/wallpaper/papers.co-vr27-building-architecture-wood-view-pattern-25-wallpaper.jpg' />
            </Grid>
            <Grid size={1.5}></Grid>
        </Grid>

    );


}

export default MediumWideImage;